import React, {useEffect, useState} from 'react';
import {connect} from 'react-redux';
import {loadCourses, saveCourse} from '../../redux/actions/courseActions';
import {loadAuthors}  from '../../redux/actions/authorActions';
import PropTypes from 'prop-types';
import CourseForm from './CourseForm';
import { newCourse } from '../../tools/mockData';
import { toast } from 'react-toastify';

function ManageCoursePage ({courses, 
    authors, 
    loadCourses, 
    loadAuthors, 
    saveCourse, 
    history,
    ...props
}){
    const [course, setCourse] = useState({...props.course});
    const [errors, setErrors] = useState({});
    const [saving, setSaving] = useState(false);

    useEffect(() => {
            if(courses.length===0){
                loadCourses().catch(error => console.log(error));} 
            else{
                setCourse({...props.course});
            }

            if(authors.length===0){
                loadAuthors().catch(error => console.log(error));
            }
        }, 
        [props.course]
    );

    const handleChange = (event) => {
        const {name, value} = event.target;
        setCourse(prevCourse => ({
            ...prevCourse,
            [name]: name==="authorId"?parseInt(value,10):value
        }));
    }

    const formIsValid = () => {
        const { title, authorId, category } = course;
        const errors= {};

        if(!title) errors.title = "Title is required";
        if(!authorId) errors.author = "Author is required";
        if(!category) errors.category = "Category is required";

        setErrors(errors);
        return Object.keys(errors).length === 0;
    }

    const handleSave = (event) => {
        event.preventDefault();
        if (!formIsValid()) return;
        setSaving(true);
        saveCourse(course).then(() => {
            toast.success('Course Saved');
            history.push("/courses")
        }).catch(error => {
            setSaving(false);
            setErrors({ onSave: error.message });
        });
    }

    return (
        <CourseForm 
            course={course} 
            authors={authors} 
            onChange={handleChange} 
            onSave={handleSave}
            saving={saving}
            errors={errors}
        />
    );

};

ManageCoursePage.propTypes = {
    course : PropTypes.object.isRequired,
    authors : PropTypes.array.isRequired,
    courses: PropTypes.array.isRequired,
    loadAuthors:  PropTypes.func.isRequired,
    loadCourses:  PropTypes.func.isRequired,
    saveCourse: PropTypes.func.isRequired,
    history : PropTypes.object.isRequired
};

const getCoursebBySlug = (courses, slug) => {
    return courses.find(course => course.slug===slug)||null;
}

function mapStateToProps(state, ownProps){ 
    const slug = ownProps.match.params.slug;
    const course = slug && state.courses.length>0? getCoursebBySlug(state.courses, slug) : newCourse;
    return {
        courses: state.courses,
        course,
        authors : state.authors
    };
}

const mapDispatchToProps= {
    loadCourses,
    loadAuthors,
    saveCourse
};
 
export default connect(mapStateToProps, mapDispatchToProps)(ManageCoursePage);